.clock {
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.clock__second-hand {
  justify-content: center;
  animation: 1s linear infinite rotate;
  display: flex;
  position: absolute;
}

.clock__second-hand:before {
  content: "";
  background: #00f;
  border-radius: 5px;
  width: 5px;
  height: 400px;
  position: absolute;
}

.clock__minute-hand {
  justify-content: center;
  animation: 60s linear infinite rotate;
  display: flex;
  position: absolute;
}

.clock__minute-hand:before {
  content: "";
  background: green;
  border-radius: 5px;
  width: 10px;
  height: 200px;
  position: absolute;
}

.clock__center {
  background-color: #000;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  position: absolute;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

